<template lang="pug">
div
  h1 
  el-row(:gutter='20')
    el-col(:span = 10)
      el-form(label-width = '80px', v-model = 'item')
        el-form-item(label = '')
          el-input(v-model = 'bar_code',placeholder = '' )
        el-form-item(label = '')
          el-input-number(v-model = 'count', :min = 1)
        el-form-item(label = '')
          el-autocomplete(v-model= 'location', placeholder = '', icon = 'edit')
          el-button(type='text',@click='showDlg("loc")', size = 'small') 
        el-form-item(label = '')
          el-autocomplete(v-model='product', icon='edit', placeholder='')
          el-button(type='text',@click='showDlg("prod")', size = 'small') 
        el-form-item(label = '')
          el-autocomplete(icon='edit', v-model = 'batch', placeholder='')
          el-button(type = 'text', @click = 'showDlg("batch")', size = 'small') 
        el-form-item(label = '')
          el-date-picker(v-model = 'item.expiry_date')
        el-form-item(label = '', label-width = '240px')
          el-switch(v-model = 'auto_print')
          el-button(type='primary')  
      el-row(type = 'flex', align = 'middle',justify = 'end')
        el-col(:span = 4)
  my-dialog(ref='dlg')
</template>
<script>
import Vue from 'vue'
import dialog from './dialogs.vue'
Vue.component('my-dialog', dialog)
export default {
  name: 'Editor',
  data: function(){
    return {
      count: 0,
      bar_code: '',
      auto_print: true,
      location: '',
      product: '',
      batch: '',
      item: {
        product_id: 0,
        location_id: 0,
        batch_id: 0,
        expiry_date: ''
      }
    }
  },
  methods:{
    showDlg(item){
      this.$refs.dlg.show(item)
    }
  }
}
</script>

<style scoped>
button{
  margin-left: 10px;
}
</style>


