<template lang='pug'>
div
  el-dialog(ref = 'locDlg', title = '')
    el-form(label-position = 'right', label-width = '60px', :model = 'loc')
      el-form-item(label = '')
        el-input(v-model = 'loc.name')
      el-form-item(label = '')
        el-autocomplete(v-model = 'loc.building', @select  = 'handleSelect', :fetch-suggestions = 'getBuildings')
    span(slot = 'footer')
      el-button(type = 'primary') 
  el-dialog(ref = 'batchDlg', title = '')
    el-form(label-position = 'right', label-width = '60px', :model = 'batch')
      el-form-item(label = '')
        el-input( disabled, v-model = 'prod.name')
      el-form-item(label = '')
        el-input(v-model = 'batch.number')
      el-form-item(label = '')
        el-date-picker(v-model = 'batch.expiry_date')
    span(slot = 'footer')
      el-button(type = 'primary') 
  el-dialog(ref='prodDlg', title = '' )
    el-form
      el-form-item(label='')
        el-input(v-model='prod.name')
      el-form-item(label='')
        el-input(v-model='prod.number')
      el-form-item(label='')
        el-input(v-model='prod.unit')
      el-form-item(label='')
        el-input(v-model='prod.spec')
    span(slot='footer')
      el-button(type = 'primary') 
</template>
<script>
  export default {
    data: function () {
      return {
        loc: {
          id: 0,
          name: '',
          building: ''
        },
        prod: {
          id: 0,
          name: ''
        },
        line: {
          product_id: 0,
          location_id: 0,
          max: 0,
          min: 0,
          current: 0
        },
        batch: {
          id: 0,
          number: '',
          expiry_date: '',
          product_id: 0
        },
      }
    },
    methods:{
      handleSelect(){
      },
      getBuildings(){
      },
      show(item){
        let dlg = this.$refs[item + 'Dlg']
        if(dlg) dlg.open()
      }
    }
  }
</script>


